.home {
  display: flex;
  position: absolute;
  top: 345vh;
  left: 0;
  width: 98%;
  text-align: center;
  justify-content: center; }

.home-grid {
  position: absolute;
  left: calc(50% - 145px);
  text-align: center;
  justify-content: center;
  z-index: 1; }

.banner-text h1 {
  font-size: 35px;
  color: #5a5959;
  font-family: "Work Sans";
  margin-bottom: 0.125em; }

.banner-text p {
  font-size: 15px;
  color: #5a5959;
  font-family: "Open Sans"; }

.banner-text br {
  content: "";
  margin: -1em;
  display: block;
  font-size: 20%; }

.social-links {
  display: flex;
  justify-content: center;
  align-items: center; }
  .social-links img {
    padding: 3px; }

.social-icon {
  height: 55px; }
  .social-icon p {
    font-family: "Open Sans"; }

.social-icon-caption {
  margin-top: -0.75em;
  font-size: 12px;
  color: #92c1f0;
  font-family: "Roboto"; }

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0; }
  .link-button:focus {
    outline: none; }
