.rocket-active {
  transform: translate(0px, -1150px); }

.exhaust-flame-active {
  position: absolute;
  top: 50%;
  width: 28px;
  background: linear-gradient(to bottom, transparent 10%, #ffca3b, #f76304 40%, #ffffff 55%);
  height: 1425px;
  left: calc(50% - 14px);
  animation: exhaust2 0.2s infinite;
  transition: all 2s;
  opacity: 70%; }

.rocket {
  position: relative;
  top: -340px;
  left: calc(50% - 31px);
  width: 80px;
  transition: all 2s;
  z-index: 0 !important; }
  .rocket .rocket-body {
    width: 80px;
    left: calc(50% - 50px);
    animation: bounce 0.5s infinite;
    transition: all 2s; }
    .rocket .rocket-body .body {
      background-color: #dadada;
      height: 180px;
      left: calc(50% - 50px);
      border-top-right-radius: 55%;
      border-top-left-radius: 55%;
      border-bottom-left-radius: 30%;
      border-bottom-right-radius: 30%; }
    .rocket .rocket-body:before {
      content: "";
      position: absolute;
      left: calc(50% - 24px);
      width: 48px;
      height: 15px;
      background-color: #584948;
      bottom: -15px;
      border-bottom-right-radius: 60%;
      border-bottom-left-radius: 60%; }
  .rocket .body-cap {
    position: absolute;
    width: 80px;
    background-color: #dd3243;
    height: 180px;
    left: calc(50% - 40px);
    border-top-right-radius: 55%;
    border-top-left-radius: 55%;
    border-bottom-left-radius: 30%;
    border-bottom-right-radius: 30%;
    clip-path: inset(0px 0px 155px 0px);
    top: 0px;
    border-top: 6px solid #ec5a69; }
  .rocket .window {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    background-color: #b4b2b2;
    left: calc(50% - 25px);
    top: 45px;
    border: 5px solid #898989; }
  .rocket .window-tint {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    left: calc(50% - 20px);
    top: 50px;
    box-shadow: inset -5px -5px 0 -4px #ffffff; }
  .rocket .fin-sides {
    position: absolute;
    z-index: -100;
    height: 65px;
    width: 40px;
    background-color: #8fc4f8; }
  .rocket .fin-left {
    left: -20px;
    top: calc(100% - 65px);
    border-top-left-radius: 85%;
    border-bottom-left-radius: 10%; }
  .rocket .fin-right {
    right: -20px;
    top: calc(100% - 65px);
    border-top-right-radius: 85%;
    border-bottom-right-radius: 10%; }
  .rocket .fin-middle {
    position: absolute;
    height: 60px;
    width: 8px;
    background-color: #8fc4f8;
    right: 36px;
    top: calc(100% - 60px);
    border-top-right-radius: 35%;
    border-top-left-radius: 35%; }
  .rocket .exhaust-flame {
    position: absolute;
    top: 100%;
    width: 28px;
    background: linear-gradient(to bottom, transparent 10%, #ffffff 65%);
    height: 260px;
    left: calc(50% - 14px);
    animation: exhaust 0.2s infinite;
    transition: all 2s; }

.exhaust-fumes {
  position: absolute;
  top: -280px;
  width: 80px;
  left: calc(50% - 51px);
  transition: all 2s; }
  .exhaust-fumes li {
    width: 60px;
    height: 60px;
    background-color: #ffffff;
    list-style: none;
    position: absolute;
    border-radius: 100%;
    transition: all 2s; }
    .exhaust-fumes li:first-child {
      width: 150px;
      height: 150px;
      bottom: -475px;
      animation: fumes 5s infinite; }
    .exhaust-fumes li:nth-child(2) {
      width: 150px;
      height: 150px;
      left: -100px;
      top: 300px;
      animation: fumes 3.2s infinite; }
    .exhaust-fumes li:nth-child(3) {
      width: 120px;
      height: 120px;
      left: -40px;
      top: 330px;
      animation: fumes 3s 1s infinite; }
    .exhaust-fumes li:nth-child(4) {
      width: 100px;
      height: 100px;
      left: -170px;
      animation: fumes 4s 2s infinite;
      top: 380px; }
    .exhaust-fumes li:nth-child(5) {
      width: 130px;
      height: 130px;
      left: -120px;
      top: 350px;
      animation: fumes 5s infinite; }
    .exhaust-fumes li:nth-child(6) {
      width: 190px;
      height: 190px;
      left: -35px;
      top: 270px;
      animation: fumes2 4s infinite; }
    .exhaust-fumes li:nth-child(7) {
      width: 100px;
      height: 100px;
      left: -85px;
      top: 315px; }
    .exhaust-fumes li:nth-child(8) {
      width: 110px;
      height: 110px;
      left: 90px;
      top: 320px; }
    .exhaust-fumes li:nth-child(9) {
      width: 100px;
      height: 100px;
      left: 150px;
      top: 375px;
      animation: fumes 3s infinite; }

@keyframes fumes {
  50% {
    transform: scale(1.5);
    background-color: transparent; }
  51% {
    transform: scale(0.8); }
  100% {
    background-color: #ffffff;
    transform: scale(1); } }

@keyframes fumes2 {
  50% {
    transform: scale(1.1); } }

@keyframes bounce {
  0% {
    transform: translate3d(0px, 0px, 0); }
  50% {
    transform: translate3d(0px, -4px, 0); }
  100% {
    transform: translate3d(0px, 0px, 0); } }

@keyframes exhaust {
  0% {
    background: linear-gradient(to bottom, transparent 10%, #ffffff 65%); }
  50% {
    background: linear-gradient(to bottom, transparent 8%, #ffffff 65%); }
  75% {
    background: linear-gradient(to bottom, transparent 12%, #ffffff 65%); } }

@keyframes exhaust2 {
  0% {
    background: linear-gradient(to bottom, transparent 11%, #ffca3b, #f76304 40%, #ffffff 55%); }
  50% {
    background: linear-gradient(to bottom, transparent 10%, #ffca3b, #f76304 40%, #ffffff 55%); }
  75% {
    background: linear-gradient(to bottom, transparent 9%, #ffca3b, #f76304 40%, #ffffff 55%); } }
